<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

		  <el-form :inline="true" :model="filters">
              <el-form-item label="">
                </el-form-item>
                <el-form-item label="">
                <el-date-picker  v-model="filters.startdate" type="datetime" placeholder="" value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                 <el-form-item label="">
                <el-date-picker  v-model="filters.enddate" type="datetime" placeholder=""  value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                <el-form-item>
					<el-button type="primary" @click="getdata"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary"    @click="exportExcel"  >Excel</el-button>
				</el-form-item>
               


			</el-form>
		</el-col>
        
		<!---->
		<el-table :data="data"    v-loading="listLoading"  style="width: 100%"  height="700" id="table" >
             <el-table-column prop="jifen" label="" width="215"  sortable></el-table-column>
             <el-table-column prop="operatedate" label="" width="215"  :formatter="dateFormat" sortable></el-table-column>
            <el-table-column prop="comname" label="" width="215" ></el-table-column>
            <el-table-column prop="comcodename" label="" width="215" ></el-table-column>
            <el-table-column prop="policyno" label="" width="215" ></el-table-column>
            <el-table-column prop="sumpremium" label="" width="215" ></el-table-column>
            <el-table-column prop="sumtaxpremium" label="" width="215" ></el-table-column>
            <el-table-column prop="sumnetpremium" label="" width="215" ></el-table-column>
            <el-table-column prop="agentname" label="" width="215" ></el-table-column>
            <el-table-column prop="clausetype" label="" width="215" ></el-table-column>
            <el-table-column prop="carkindname" label="" width="215" ></el-table-column>
            <el-table-column prop="startdate" label="" width="215" ></el-table-column>
            <el-table-column prop="enddate" label="" width="215" ></el-table-column>
            <el-table-column prop="licenseno" label="" width="215" ></el-table-column>
            <el-table-column prop="businessnaturename" label="" width="215" ></el-table-column>
            <el-table-column prop="riskcode" label="" width="215" ></el-table-column>
            <el-table-column prop="monopolycode" label="" width="215" ></el-table-column>
            <el-table-column prop="monopolyname" label="" width="215" ></el-table-column>
            <el-table-column prop="handler1name" label="" width="215" ></el-table-column>
            <el-table-column prop="handlername" label="" width="215" ></el-table-column>
            <el-table-column prop="policynotime" label="" width="215" ></el-table-column>
            <el-table-column prop="projectcode" label="" width="215" ></el-table-column>
            <el-table-column prop="insuredname" label="" width="215" ></el-table-column>
            <el-table-column prop="bbrinsuredname" label="" width="215" ></el-table-column>
            <el-table-column prop="xinxuzhuan" label="" width="215" ></el-table-column>
            <el-table-column prop="mainsellfeerate" label="" width="215" ></el-table-column>
            <el-table-column prop="sellfeerate" label="" width="215" ></el-table-column>
            <el-table-column prop="summainsellfeerate" label="" width="215" ></el-table-column>
            <el-table-column prop="sumsellfeerate" label="" width="215" ></el-table-column>
            <el-table-column prop="maxcostrate" label="" width="215" ></el-table-column>
            <el-table-column prop="lastpolicyno" label="" width="215" ></el-table-column>
            <el-table-column prop="usenaturename" label="" width="215" ></el-table-column>
            <el-table-column prop="insuredtype" label="" width="215" ></el-table-column>
            <el-table-column prop="clausecode" label="" width="215" ></el-table-column>
            <el-table-column prop="clausename" label="" width="215" ></el-table-column>
            <el-table-column prop="clauses" label="" width="215" ></el-table-column>
		</el-table>




	</section>
</template>

<script>

import FileSaver from 'file-saver'
import XLSX from 'xlsx'
import moment from "moment"
	export default {
		data() {
			return {
				filters: {
                    startdate:'',
                    enddate: '',
				},
                data:[],
                listLoading:false, 
                 }
		    },
		methods: {
	//     exportExcel(){
    //         var url = "http://56.145.32.32/etl/data/.xls"
    //         window.open(url);
    // },
    		// excle
		 exportExcel () {
         /* generate workbook object from table */
		 var wb = XLSX.utils.table_to_book(document.getElementById('table'))
         /* get binary string as output */
         var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
         try {
             FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '.xlsx')
         } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
         return wbout
       },
			//
			getdata:function() {
                if(this.checkdate()){              
				let para = { 
                    startdate:this.filters.startdate,
                    enddate:this.filters.enddate  
				};
                this.listLoading = true;
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/lipei/baoan',para,{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/kefu/jifenchansheng',para,{ emulateJSON: true }).then((res) => {
                    this.data = res.data.data;
					this.listLoading = false;
                    this.$message({
                        message: '',
                         type: 'success'
                     });					
                   }).catch((err)=>{
                    this.$message({
                        message: '',
                         type: 'error'
                     });
                     this.listLoading = false;
                   });
                 }
            },
         dateFormat:function(row, column) {  
               var date = row[column.property];  
          if (date == undefined) {  
             return "";  
          }  
          return moment(date).format("YYYY-MM-DD HH:mm:ss");  
        },
            checkdate(){
                if(this.filters.startdate=='' || this.filters.enddate==''){
                    this.$alert('');
                    return false;
                } else{
                    return true;
                }   
            },

        },
	}

</script>

<style scoped>

</style>


