<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

		  <!-- <el-form :inline="true" :model="filters">
              <el-form-item label="">
                </el-form-item>
                <el-form-item label="">
                <el-date-picker  v-model="filters.startdate" type="datetime" placeholder="" value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                 <el-form-item label="">
                <el-date-picker  v-model="filters.enddate" type="datetime" placeholder=""  value-format="yyyy-MM-dd HH:mm:ss" ></el-date-picker>
                </el-form-item>

                <el-form-item>
					<el-button type="primary" @click="getdata"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary"    @click="exportExcel"  >Excel</el-button>
				</el-form-item>
               


			</el-form> -->
		</el-col>
        
		<!---->
		<el-table :data="data"    v-loading="listLoading"  style="width: 100% height:100%"   id="table" >
		
            <el-table-column prop="ip" label="IP" width="215"  ></el-table-column>

            <el-table-column prop="name" label="" width="215" ></el-table-column>

            <el-table-column prop="dep" label="" width="215" ></el-table-column>


             <el-table-column prop="remark" label="" mid-width="215" ></el-table-column>

            <el-table-column label="" mid-width="215" >
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>


    <!--  -->
		</el-table>
            <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px"  ref="editForm">

				<el-form-item label="IP" prop="ip">
					<el-input v-model="editForm.ip" auto-complete="off" readonly="readonly"></el-input>
				</el-form-item>

				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off" ></el-input>
				</el-form-item>

                <el-form-item label="" prop="dep">
					<el-input v-model="editForm.dep" auto-complete="off" ></el-input>
				</el-form-item>

				<el-form-item label="" prop="remark">
					<el-input v-model="editForm.remark" auto-complete="off" ></el-input>
				</el-form-item>

				
				</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>



	</section>
</template>

<script>

import FileSaver from 'file-saver'
import XLSX from 'xlsx'
import moment from "moment"
	export default {
		data() {
			return {

                //
				editForm: {
                    ip: '',
                    name:'',
                    dep:'',
                    remark:''
                },
                sels: [],//
				editFormVisible: false,//
                data:[],
                listLoading:false, 
                editLoading: false,
                 }
		    },
		methods: {
	//     exportExcel(){
    //         var url = "http://56.145.32.32/etl/data/.xls"
    //         window.open(url);
    // },
            // excle
            
		 exportExcel () {
         /* generate workbook object from table */
		 var wb = XLSX.utils.table_to_book(document.getElementById('table'))
         /* get binary string as output */
         var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
         try {
             FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '.xlsx')
         } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
         return wbout
       },
               	//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			getdata:function() {
                          
                this.listLoading = true;
                // 56.145.32.54:8081
				// this.$http.post('http://56.145.32.63:8080/xinxijishubu/getipaddress',{ emulateJSON: true }).then((res) => {
                this.$http.post('http://56.145.32.32:8082/xinxijishubu/getipaddress',{ emulateJSON: true }).then((res) => {
                    this.data = res.data.data;
					this.listLoading = false;
                    this.$message({
                        message: '',
                         type: 'success'
                     });					
                   }).catch((err)=>{
                    this.$message({
                        message: '',
                         type: 'error'
                     });
                     this.listLoading = false;
                   });
                 
            },
            			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							// this.$http.post('http://56.145.32.63:8080/xinxijishubu/updateipaddress',para,{ emulateJSON: true }).then((res) => {
							this.$http.post('http://56.145.32.32:8082/xinxijishubu/updateipaddress',para,{ emulateJSON: true }).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.code = res.data.code;
								if(this.code==1){
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getdata();
								}else{
									this.$message({
									message: '',
									type: 'error'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getdata();
								}

							});
						});
					}
				});
			},


        },

         mounted() {
			this.getdata();
		}

	}

</script>

<style scoped>

</style>


